"use client"

import { useEffect, useRef, useState } from "react"
import { Playfair_Display, Inter } from "next/font/google"
import * as THREE from "three"
import { GLTFLoader } from "three/addons/loaders/GLTFLoader.js"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

gsap.registerPlugin(ScrollTrigger)

const displayFont = Playfair_Display({ subsets: ["latin"], weight: ["500", "600", "700"] })
const bodyFont = Inter({ subsets: ["latin"], weight: ["400", "500", "600"] })

/**
 * IMPORTANT:
 * These shots are expressed as MULTIPLIERS of the model "radius" (computed from bounding sphere).
 * That makes them stable even if your GLB scale changes.
 *
 * If a feature is slightly off, tweak the multipliers OR tweak the lookAt offsets.
 */
type Shot = {
  name: string
  label: string
  headline: string
  description: string

  // wide "establishing" view for that section
  wide: { cam: [number, number, number]; look: [number, number, number]; rotY?: number }

  // close "feature" view for that section (zoom in)
  close: { cam: [number, number, number]; look: [number, number, number]; rotY?: number }
}

const SHOTS: Shot[] = [
  {
    name: "Wind Turbines",
    label: "FEATURE 01",
    headline: "Wind Turbines",
    description: "Advanced vertical-axis turbines harvest wind energy at altitude, powering sustainable flight.",
    // Side-ish view, centered
    wide: { cam: [1.8, 0.55, 1.8], look: [0.0, 0.1, 0.0], rotY: 0.2 },
    // Zoom into the side turbine area (assumes turbines are on the “side”)
    close: { cam: [1.05, 0.35, 1.15], look: [0.35, 0.15, 0.05], rotY: 0.25 },
  },
  {
    name: "Carbon Sails",
    label: "FEATURE 02",
    headline: "Carbon Sails",
    description: "Ultra-lightweight carbon fiber sails maximize lift efficiency while minimizing structural weight.",
    // Slightly higher wide shot
    wide: { cam: [-1.9, 0.7, 1.6], look: [0.0, 0.18, 0.0], rotY: 0.55 },
    // Zoom to top (sails on top)
    close: { cam: [-1.15, 1.0, 0.9], look: [0.0, 0.65, 0.0], rotY: 0.6 },
  },
  {
    name: "Propeller Pods",
    label: "FEATURE 03",
    headline: "Propeller Pods",
    description: "Gimbaled electric propulsion pods provide precise thrust vectoring for unmatched maneuverability.",
    // Front-ish wide
    wide: { cam: [0.0, 0.55, 2.25], look: [0.0, 0.15, 0.0], rotY: 0.75 },
    // Zoom into nose-front prop area (assumes prop is closer to nose/front)
    close: { cam: [0.15, 0.35, 1.1], look: [0.2, 0.1, 0.2], rotY: 0.75 },
  },
  {
    name: "Stabilizing Fins",
    label: "FEATURE 04",
    headline: "Stabilizing Fins",
    description: "Active stabilization fins with AI-powered control maintain perfect balance in turbulent conditions.",
    // Rear/underside wide
    wide: { cam: [-1.9, 0.35, -1.6], look: [0.0, 0.15, 0.0], rotY: 1.15 },
    // Zoom into rear fin area
    close: { cam: [-1.1, 0.25, -0.95], look: [-0.2, 0.1, -0.35], rotY: 1.15 },
  },
  {
    name: "Structural Frame",
    label: "FEATURE 05",
    headline: "Structural Frame",
    description: "Aerospace-grade titanium alloy skeleton ensures maximum strength with minimal weight.",
    // Diagonal wide
    wide: { cam: [1.8, 0.8, -1.7], look: [0.0, 0.2, 0.0], rotY: 1.45 },
    // Slight zoom toward mid-body
    close: { cam: [1.05, 0.55, -1.05], look: [0.0, 0.15, -0.2], rotY: 1.45 },
  },
]

function clamp(n: number, a: number, b: number) {
  return Math.max(a, Math.min(b, n))
}

export default function AirshipDemo() {
  const containerRef = useRef<HTMLDivElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const heroTextRef = useRef<HTMLDivElement>(null)
  const featureRefs = useRef<(HTMLDivElement | null)[]>([])
  const footerRef = useRef<HTMLDivElement>(null)

  const targetCameraPosition = useRef(new THREE.Vector3(10, 5, 10)).current
  const targetLookAt = useRef(new THREE.Vector3(0, 0, 0)).current
  const currentLookAt = useRef(new THREE.Vector3(0, 0, 0)).current

  const [isLoaded, setIsLoaded] = useState(false)
  const [loadProgress, setLoadProgress] = useState(0)

  useEffect(() => {
    if (!containerRef.current || !canvasRef.current) return

    const prevOverflow = document.body.style.overflow
    document.body.style.overflow = "hidden"

    // Scene
    const scene = new THREE.Scene()
    scene.background = new THREE.Color(0x000000)

    // Camera
    const camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 2000)

    // Renderer
    const renderer = new THREE.WebGLRenderer({
      canvas: canvasRef.current,
      antialias: true,
      alpha: false,
    })
    renderer.setSize(window.innerWidth, window.innerHeight)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    renderer.physicallyCorrectLights = true
    renderer.toneMapping = THREE.ACESFilmicToneMapping
    renderer.toneMappingExposure = 0.78 // ↓ reduces harsh contrast / “too bright”
    renderer.outputColorSpace = THREE.SRGBColorSpace

    // Lights (softer, less blown out)
    const ambient = new THREE.AmbientLight(0xffffff, 0.22)
    scene.add(ambient)

    const hemisphere = new THREE.HemisphereLight(0xffffff, 0x101010, 0.18)
    scene.add(hemisphere)

    const keyLight = new THREE.DirectionalLight(0xffffff, 0.95)
    keyLight.position.set(6, 10, 6)
    scene.add(keyLight)

    const fillLight = new THREE.DirectionalLight(0x88ccff, 0.22)
    fillLight.position.set(-7, 3, -6)
    scene.add(fillLight)

    const rimLight = new THREE.DirectionalLight(0xffddaa, 0.28)
    rimLight.position.set(-4, 3, 9)
    scene.add(rimLight)

    // Model + framing
    let airshipModel: THREE.Group | null = null
    let modelCenter = new THREE.Vector3(0, 0, 0)
    let modelRadius = 5 // fallback until computed
    let baseYaw = 0

    const loader = new GLTFLoader()

    // --- Helpers to apply “multiplier” shots ---
    const applyShot = (shot: { cam: [number, number, number]; look: [number, number, number]; rotY?: number }) => {
      // camera position = center + (mult * radius)
      targetCameraPosition.set(
        modelCenter.x + shot.cam[0] * modelRadius,
        modelCenter.y + shot.cam[1] * modelRadius,
        modelCenter.z + shot.cam[2] * modelRadius,
      )

      // lookAt target = center + (offset * radius)
      targetLookAt.set(
        modelCenter.x + shot.look[0] * modelRadius,
        modelCenter.y + shot.look[1] * modelRadius,
        modelCenter.z + shot.look[2] * modelRadius,
      )

      if (airshipModel && typeof shot.rotY === "number") {
        // Keep rotations stable and additive around Y
        airshipModel.rotation.y = baseYaw + shot.rotY
      }
    }

    // --- Build scroll after model load (critical to avoid “model stuck” issues) ---
    let gsapCtx: gsap.Context | null = null
    const setupScroll = () => {
      ScrollTrigger.getAll().forEach((t) => t.kill())

      gsapCtx = gsap.context(() => {
        // HERO: start zoomed out + centered (your screenshot shows top-right too big)
        const heroTl = gsap.timeline({
          scrollTrigger: {
            trigger: heroTextRef.current,
            start: "top top",
            end: "bottom top",
            scrub: 1,
          },
        })

        // Wide, centered hero view
        heroTl.to(
          {},
          {
            duration: 1,
            onUpdate: () => {
              // a stable, centered, zoomed-out view
              applyShot({
                cam: [0.0, 0.55, 2.35],
                look: [0.0, 0.18, 0.0],
                rotY: 0.0,
              })
            },
          },
          0,
        )

        // Hero text fade
        heroTl.to(
          heroTextRef.current,
          {
            opacity: 0,
            y: -50,
          },
          0,
        )

        // FEATURES: each section does:
        // wide -> close (zoom in) -> wide (zoom out) as you scroll through the section
        SHOTS.forEach((shot, index) => {
          const featureEl = featureRefs.current[index]
          if (!featureEl) return

          const tl = gsap.timeline({
            scrollTrigger: {
              trigger: featureEl,
              start: "top center",
              end: "bottom center",
              scrub: 1.8,
            },
          })

          // At section start: wide establish
          tl.to(
            {},
            {
              duration: 0.01,
              onComplete: () => applyShot(shot.wide),
            },
            0,
          )

          // Zoom into the feature (midway)
          tl.to(
            {},
            {
              duration: 0.55,
              onUpdate: () => {
                // smoothly interpolate wide -> close
                const p = tl.progress()
                const t = clamp((p - 0.05) / 0.45, 0, 1)

                const cam = new THREE.Vector3(
                  THREE.MathUtils.lerp(shot.wide.cam[0], shot.close.cam[0], t),
                  THREE.MathUtils.lerp(shot.wide.cam[1], shot.close.cam[1], t),
                  THREE.MathUtils.lerp(shot.wide.cam[2], shot.close.cam[2], t),
                )
                const look = new THREE.Vector3(
                  THREE.MathUtils.lerp(shot.wide.look[0], shot.close.look[0], t),
                  THREE.MathUtils.lerp(shot.wide.look[1], shot.close.look[1], t),
                  THREE.MathUtils.lerp(shot.wide.look[2], shot.close.look[2], t),
                )
                const rotY = THREE.MathUtils.lerp(shot.wide.rotY ?? 0, shot.close.rotY ?? 0, t)

                applyShot({
                  cam: [cam.x, cam.y, cam.z],
                  look: [look.x, look.y, look.z],
                  rotY,
                })
              },
            },
            0,
          )

          // Zoom back out (close -> wide) toward the end of the section
          tl.to(
            {},
            {
              duration: 0.55,
              onUpdate: () => {
                const p = tl.progress()
                const t = clamp((p - 0.55) / 0.45, 0, 1)

                const cam = new THREE.Vector3(
                  THREE.MathUtils.lerp(shot.close.cam[0], shot.wide.cam[0], t),
                  THREE.MathUtils.lerp(shot.close.cam[1], shot.wide.cam[1], t),
                  THREE.MathUtils.lerp(shot.close.cam[2], shot.wide.cam[2], t),
                )
                const look = new THREE.Vector3(
                  THREE.MathUtils.lerp(shot.close.look[0], shot.wide.look[0], t),
                  THREE.MathUtils.lerp(shot.close.look[1], shot.wide.look[1], t),
                  THREE.MathUtils.lerp(shot.close.look[2], shot.wide.look[2], t),
                )
                const rotY = THREE.MathUtils.lerp(shot.close.rotY ?? 0, shot.wide.rotY ?? 0, t)

                applyShot({
                  cam: [cam.x, cam.y, cam.z],
                  look: [look.x, look.y, look.z],
                  rotY,
                })
              },
            },
            0,
          )

          // Text fade in
          tl.fromTo(
            featureEl.querySelector(".feature-content"),
            { opacity: 0, y: 30 },
            { opacity: 1, y: 0, duration: 0.35, ease: "power2.out" },
            0.15,
          )
        })

        // FOOTER: wrap up zoomed out, centered “hero” view again
        if (footerRef.current) {
          const endTl = gsap.timeline({
            scrollTrigger: {
              trigger: footerRef.current,
              start: "top center",
              end: "bottom center",
              scrub: 1,
            },
          })

          endTl.to(
            {},
            {
              duration: 1,
              onUpdate: () => {
                applyShot({
                  cam: [0.0, 0.6, 2.55],
                  look: [0.0, 0.18, 0.0],
                  rotY: 0.0,
                })
              },
            },
            0,
          )
        }
      }, containerRef)

      ScrollTrigger.refresh()
    }

    // Optional: click-to-log feature point in world space (helps you dial in lookAt offsets)
    const raycaster = new THREE.Raycaster()
    const mouse = new THREE.Vector2()
    const onClick = (e: MouseEvent) => {
      if (!airshipModel) return
      const rect = renderer.domElement.getBoundingClientRect()
      mouse.x = ((e.clientX - rect.left) / rect.width) * 2 - 1
      mouse.y = -(((e.clientY - rect.top) / rect.height) * 2 - 1)
      raycaster.setFromCamera(mouse, camera)
      const hits = raycaster.intersectObject(airshipModel, true)
      if (hits[0]) {
        const p = hits[0].point.clone()
        const rel = p.sub(modelCenter).divideScalar(modelRadius)
        console.log("[pick] world:", hits[0].point)
        console.log("[pick] relative (look offsets):", { x: rel.x, y: rel.y, z: rel.z })
      }
    }
    window.addEventListener("click", onClick)

    loader.load(
      "/airship.glb",
      (gltf) => {
        airshipModel = gltf.scene
        scene.add(airshipModel)

        // Center the model to origin
        const box = new THREE.Box3().setFromObject(airshipModel)
        const center = box.getCenter(new THREE.Vector3())
        airshipModel.position.sub(center)

        // Recompute bounds after centering
        const box2 = new THREE.Box3().setFromObject(airshipModel)
        const sphere = box2.getBoundingSphere(new THREE.Sphere())

        modelCenter = sphere.center.clone() // should be near 0,0,0 now
        modelRadius = Math.max(0.0001, sphere.radius)

        // If your GLB is gigantic, uncomment and adjust:
        // airshipModel.scale.setScalar(0.5)
        // Then recompute sphere again if you do that.

        // Base yaw (so we can rotate shots consistently)
        baseYaw = airshipModel.rotation.y

        // Start: centered, zoomed out (fixes “top-right too big”)
        applyShot({ cam: [0.0, 0.55, 2.35], look: [0.0, 0.18, 0.0], rotY: 0.0 })
        camera.position.copy(targetCameraPosition)
        currentLookAt.copy(targetLookAt)
        camera.lookAt(currentLookAt)

        // Build scroll timelines now that model exists
        setupScroll()

        // Finish loading
        setLoadProgress(100)
        setTimeout(() => {
          setIsLoaded(true)
          document.body.style.overflow = prevOverflow || "auto"
        }, 250)
      },
      (progress) => {
        const pct =
          progress.total && progress.total > 0
            ? (progress.loaded / progress.total) * 100
            : Math.min(95, loadProgress + 1)
        setLoadProgress((p) => Math.max(p, Math.round(pct)))
      },
      (error) => {
        console.error("[v0] GLB load failed:", error)

        // Placeholder so you can still scroll/see layout
        const geometry = new THREE.CylinderGeometry(1, 1, 4, 32)
        const material = new THREE.MeshStandardMaterial({ color: 0x888888, metalness: 0.4, roughness: 0.6 })
        const placeholder = new THREE.Mesh(geometry, material)
        placeholder.rotation.z = Math.PI / 2
        scene.add(placeholder)
        airshipModel = placeholder as unknown as THREE.Group

        modelCenter.set(0, 0, 0)
        modelRadius = 4

        setupScroll()

        setLoadProgress(100)
        setTimeout(() => {
          setIsLoaded(true)
          document.body.style.overflow = prevOverflow || "auto"
        }, 250)
      },
    )

    // Render loop (correct look-at smoothing)
    let raf = 0
    const animate = () => {
      raf = requestAnimationFrame(animate)

      camera.position.lerp(targetCameraPosition, 0.075)
      currentLookAt.lerp(targetLookAt, 0.12)
      camera.lookAt(currentLookAt)

      renderer.render(scene, camera)
    }
    animate()

    const handleResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight
      camera.updateProjectionMatrix()
      renderer.setSize(window.innerWidth, window.innerHeight)
      ScrollTrigger.refresh()
    }
    window.addEventListener("resize", handleResize)

    // Debug: press C to log current cam/look in multipliers
    const onKey = (e: KeyboardEvent) => {
      if (e.key === "c" || e.key === "C") {
        const camRel = camera.position.clone().sub(modelCenter).divideScalar(modelRadius)
        const lookRel = currentLookAt.clone().sub(modelCenter).divideScalar(modelRadius)
        console.log("[cam] relative:", { x: camRel.x, y: camRel.y, z: camRel.z })
        console.log("[look] relative:", { x: lookRel.x, y: lookRel.y, z: lookRel.z })
        console.log("[radius]", modelRadius)
      }
    }
    window.addEventListener("keypress", onKey)

    return () => {
      cancelAnimationFrame(raf)
      window.removeEventListener("resize", handleResize)
      window.removeEventListener("keypress", onKey)
      window.removeEventListener("click", onClick)

      try {
        gsapCtx?.revert()
      } catch {}
      ScrollTrigger.getAll().forEach((t) => t.kill())
      renderer.dispose()

      document.body.style.overflow = prevOverflow
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div ref={containerRef} className={`${bodyFont.className} relative`}>
      {/* Premium Loading Overlay */}
      <div
        className={[
          "fixed inset-0 z-[999] flex items-center justify-center",
          "bg-black/80 backdrop-blur-xl",
          "transition-opacity duration-500",
          isLoaded ? "opacity-0 pointer-events-none" : "opacity-100",
        ].join(" ")}
      >
        <div className="w-full max-w-xl px-8">
          <div className="flex items-center justify-between mb-6">
            <div className="text-white/70 text-xs tracking-[0.35em] uppercase">Initializing</div>
            <div className="text-white/60 text-xs tracking-[0.25em] tabular-nums">{loadProgress}%</div>
          </div>

          <div className={`${displayFont.className} text-white text-4xl md:text-5xl font-semibold tracking-tight`}>
            Airship
          </div>
          <div className="mt-3 text-white/65 text-sm leading-relaxed">
            Loading model and lighting for a smooth scroll experience.
          </div>

          <div className="mt-8">
            <div className="h-[2px] w-full bg-white/10 overflow-hidden rounded-full">
              <div
                className="h-full bg-white/70 rounded-full transition-[width] duration-200"
                style={{ width: `${Math.min(100, Math.max(0, loadProgress))}%` }}
              />
            </div>

            <div className="mt-5 flex items-center gap-3 text-white/45 text-xs">
              <div className="h-3 w-3 rounded-full border border-white/30 border-t-white/80 animate-spin" />
              <span className="tracking-[0.18em] uppercase">Please wait</span>
            </div>

            <div className="mt-6 text-white/35 text-xs leading-relaxed">
              Tip: click the model to log a feature point (relative offsets) in the console so you can dial in turbine/sail
              targets precisely.
            </div>
          </div>
        </div>
      </div>

      {/* Fixed 3D Canvas */}
      <canvas ref={canvasRef} className="fixed inset-0 w-full h-full" />

      {/* Navigation */}
      <nav className="fixed top-0 left-0 right-0 z-50 px-8 py-6">
        <div className="flex items-center justify-between max-w-screen-2xl mx-auto">
          <div className={`${displayFont.className} text-white text-xl font-semibold tracking-tight`}>AIRSHIP</div>
          <div className="flex items-center gap-8">
            <a href="#" className="text-white/70 hover:text-white text-sm transition-colors">
              Overview
            </a>
            <a href="#" className="text-white/70 hover:text-white text-sm transition-colors">
              Specs
            </a>
            <a href="#" className="text-white/70 hover:text-white text-sm transition-colors">
              Contact
            </a>
          </div>
        </div>
      </nav>

      {/* Scrollable content */}
      <div className="relative z-10">
        {/* Hero */}
        <section className="h-screen flex items-center justify-center">
          <div ref={heroTextRef} className="text-center px-8">
            <div className="text-sm text-white/60 mb-4 tracking-[0.2em] uppercase">The Future of Flight</div>
            <h1
              className={`${displayFont.className} text-7xl md:text-8xl lg:text-9xl font-bold text-white mb-6 tracking-tight text-balance`}
            >
              Airship
            </h1>
            <p className="text-xl md:text-2xl text-white/80 max-w-2xl mx-auto text-pretty">
              Reimagining sustainable aviation with cutting-edge design and engineering
            </p>
            <div className="mt-12 flex items-center justify-center gap-2 text-white/50 text-sm">
              <div className="w-px h-12 bg-white/30 animate-bounce" />
              <span>Scroll to explore</span>
            </div>
          </div>
        </section>

        {/* Features */}
        {SHOTS.map((shot, index) => (
          <section
            key={shot.name}
            ref={(el) => {
              featureRefs.current[index] = el
            }}
            className="h-screen flex items-center justify-end px-8 md:px-16 lg:px-24"
          >
            <div className="feature-content max-w-xl">
              <div className="text-xs text-white/50 mb-3 tracking-[0.3em] uppercase">{shot.label}</div>
              <h2
                className={`${displayFont.className} text-5xl md:text-6xl lg:text-7xl font-bold text-white mb-6 tracking-tight text-balance`}
              >
                {shot.headline}
              </h2>
              <p className="text-lg md:text-xl text-white/70 leading-relaxed text-pretty">{shot.description}</p>
            </div>
          </section>
        ))}

        {/* Footer */}
        <section ref={footerRef} className="h-screen flex items-center justify-center px-8">
          <div className="text-center max-w-3xl">
            <h2
              className={`${displayFont.className} text-6xl md:text-7xl font-bold text-white mb-6 tracking-tight text-balance`}
            >
              Experience the future
            </h2>
            <p className="text-xl text-white/70 mb-12 text-pretty">
              Reserve your airship today and be part of the sustainable aviation revolution
            </p>
            <button className="bg-white text-black px-8 py-4 rounded-full text-lg font-semibold hover:bg-white/90 transition-colors">
              Reserve Now
            </button>
          </div>
        </section>
      </div>
    </div>
  )
}
